

.modal {
 @media screen and( min-width: 100px){
    border-radius: 10px;
    width: 70%;
   height: 90%;
    position: absolute;

 & .about__item-discription{
     @media screen and( max-width: 768px){
        display: none;
     }

 }
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: var(--background-color);
       background-image: url(../image/about/mobile/sectionbg1.png);

    background-repeat: no-repeat;
    background-size: contain;
    background-position: bottom;


    transition: scale 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
 }
    
    @media screen and( min-width: 480px) {
    width: 70%;
    height: 90%;
    }
    @media screen and( min-width: 1200px) {
        width: 66%;
        max-height: 100vh;
    }

}
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    opacity: 1;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
}


.button-close {
   cursor: pointer;
    display: flex;
    position: absolute;
    top: 8px;
    right: 8px;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    align-items: center;
    background-color: #d41443;
    justify-content: center;
    border: 1px solid var(--accent-font);
    box-sizing: border-box;
    transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,&:focus{
        box-shadow: -1px 1px 12px 18px rgb(212 20 67 / 10%);
}
.button-close__icon {
   fill: var(--accent-font); 
    width: 14px;
    height: 14px;
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    
    }
}

.modal__form {
    position: relative;
    padding: 60px;
    & a {
        color: var(--accent-color); 
    }
}
