.header {
  position: absolute;
  width: 100%;
  padding-top: 6px;
  background: transparent;
  left: 0;
  z-index: 4;

  @media screen and (min-width: 768px) {
    padding-top: 37px;
  }
}

.header__list {
  display: none;

  @media screen and (min-width: 1280px) {
    display: flex;
  }
}

.header__list--link {
  font-family: var(--sans-serif);
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
  display: flex;
  align-items: center;
  color: var(--alt-text-color);

  &:hover,
  :focus {
    color: var(--accent-color);
  }
}

.header__list--item {
  &:not(:last-child) {
    margin-right: 35px;
  }
}

.header__list--link--current {
  color: var(--accent-color);
}

.header__button {
  display: none;
  width: 160px;
  height: 44px;
  border-radius: 22px;
  border: transparent;
  cursor: pointer;
  background: var(--background-color);
  margin: 0;

  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.header__button--span {
  margin-right: 12px;
}

.nav-open {
  display: block;
  border: none;
  background: transparent;

  @media screen and (min-width: 768px) {
    margin-left: auto;
    margin-right: 29px;
  }

  @media screen and (min-width: 1280px) {
    display: none;
  }
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu-container {
  width: 100vw;
  height: 100vh;
  background: #e17992;
  position: fixed;
  top: 0;
  left: 0;
  padding: 23px 20px 0 21px;
  @media screen and (min-width: 768px) {
    width: 254px;
    right: 0;
    left: unset;
  }
  @media screen and (min-width: 1280px) {
    display: none;
  }

  &.is-hidden {
    display: none;
    //  opacity: 0;
    //  appearance: none;
  }
}

.menu__button--close {
  background: transparent;
  border: transparent;
  display: block;
  margin-left: auto;
  margin-bottom: 23px;
}

.menu-buy__button {
  width: 209px;
  display: flex;
  margin-bottom: 19px;
}

.menu-container__link {
  display: block;
  padding-bottom: 11px;
  padding-top: 13px;
  font-family: var(--sans-serif);
  font-weight: 700;
  font-size: 14px;
  line-height: 1.28;
  // display: flex;
  // align-items: center;

  color: var(--alt-text-color);
  transition: color 1000ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,:focus {
    color: var(--accent-color);
  }
}

.menu-container__link--current {
  color: var(--accent-color);
  
}

.menu-container__item {
  border-bottom: 1px solid #e18298;
}
